

import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Panel from 'vux/src/components/panel/index.vue'

import scroll from "@/base/scroll/scroll.vue";
import { getBanner, getPersonalized } from "@/api/api";
export default {
data() {
return {
banners: [],
recommendList: []
};
},

beforeCreate() {},

created() {
this._getBanner();
this._getPersonalized();
},

beforeMount() {},

mounted() {},

beforeUpdate() {},

updated() {},

beforeDestroy() {},

destroyed() {},

methods: {
_getBanner() {
this.$vux.loading.show({
text: "Loading"
});
getBanner().then(res => {
this.$vux.loading.hide();
this.banners = res.banners;
});
},
_getPersonalized() {
getPersonalized().then(res => {
this.recommendList = res.result.map(item => {
return {
title: item.name,
desc: item.copywriter,
src: item.picUrl,
id: item.id
};
});
});
},
handleListClick(item) {
console.log(item);

this.$router.push({
path: `/recommend/${item.id}`
});
},

},
computed: {

},
components: {
Swiper,
SwiperItem,
Panel,
scroll
}
};



